  },
  eventStatus: "LIVE" as const,
  isEventAvailableForUser: true,
  isEventAvailableInUserRegion: true,
  commerceCoverage: null,
} as unknown as IModelEvent);

// Stream source input
const streamUrl = ref(mockEvent.value.source.hd);

// Apply new stream URL
